.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.weight-canvas {
  width: 100%;
  max-width: 1000px;
  margin-bottom: 1em;
}
#weight-buttons {
  max-width: 210px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: center;
}
.button-container {
  border: 1px black solid;
}
.button-container p {
  display: inline-block;
  margin: 0;
  margin-right: 1em;
}
.plate-icon {
  max-height: 75px;
}
.remove-button {
  display: inline;
}